@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
*{box-sizing:border-box;margin:0;padding:0}:root{--primary-color: #92e6dc;--secondary-color: #65c0aa;--mixing-color: #658fc0;--mixing-color2: #23e6aa;--footer-background: #141418;--gradient-color: rgba(101, 143, 192, 0.7);--gradient-color2: rgba(35, 230, 170, 0.7);--font-color: #1a1a1b;--font-color2: #ffffff}body{font-family:'Raleway', sans-serif}html{height:100%}


.slider{width:100%;display:flex;align-items:center;overflow:hidden}.slide{min-width:100%;height:40.625rem;overflow:hidden}.btnLeft,.btnRight{position:absolute;top:60%;transform:translateY(-50%);width:2.063rem;height:2.063rem;transition:0.5s;margin:0 2rem 0 2rem}@media (max-width: 767px){.btnLeft,.btnRight{visibility:hidden}}.btnLeft{left:0}.btnRight{right:0}.pe{display:flex;justify-self:center;z-index:7}.container-center{width:100%;display:flex;justify-content:center}@media (min-width: 992px){.container-center{top:104px;align-items:center}}.center-text{color:#fff;position:absolute;font-size:2.5rem;top:160px}@media (max-width: 767px){.center-text{font-size:1.125rem;top:80px}}.center-text h1{text-align:center}.center-text p{margin:2rem 0 0 0;text-align:center}

.nav{display:flex;flex-direction:row;justify-content:space-around;list-style:none;background:var(--primary-color)}@media (max-width: 767px){.nav{flex-direction:column;justify-content:center;height:100%}}.nav__logo{margin:1.3rem 0 0 0}@media (max-width: 767px){.nav__logo{display:flex;align-items:center;justify-content:center}}.nav__list{display:flex;list-style:none}@media (max-width: 767px){.nav__list{flex-direction:column;align-items:center}}.nav__item{margin:1.875rem 3rem 0 0;text-transform:uppercase}@media (max-width: 767px){.nav__item{margin:1.875rem 0 0 0}}.nav a:link,.nav a:visited{color:var(--font-color2);text-decoration:none}.nav a:hover,.nav a:active{border-bottom:solid #ffffff 2px}

.services{display:flex;justify-content:center}@media (max-width: 767px){.services{margin:-16rem 0 1rem 0}}.container{display:flex;flex-direction:row;justify-content:space-between;width:70vw;height:95%;margin:4.688rem 0 2.5rem 0}@media (max-width: 767px){.container{flex-direction:column;align-items:center;margin:0 0 0 0}}.rightColumn{display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(2, 1fr);grid-column-gap:1.5rem;grid-row-gap:1.4rem;margin:0 0 0 0}@media (max-width: 767px){.rightColumn{grid-template-columns:1fr}}.rightColumn__box{height:16.75rem;width:12rem;display:flex;flex-direction:column;font-size:12px}.rightColumn h3{text-transform:uppercase;margin:1.4rem 0 0 0}.leftColumn{display:flex;flex-direction:column;width:12rem}

.container-video{width:100%;height:100%;background-image:url(/tourist/static/media/bg.27084291.png);background-size:cover;display:flex;flex-direction:column;justify-content:center;position:relative}.container-video__content{display:flex;flex-direction:column;align-self:center;margin:0 0 5rem 0;color:#fff;z-index:3}.container-video__content h3{display:flex;justify-content:center;margin:0 0 1rem 0}.container-video__videoitem{display:flex;flex-direction:column;align-self:center}.container-video__overlay{position:absolute;left:0;top:0;right:0;bottom:0;background:rgba(0,0,0,0.9);z-index:2}.video-frame{width:41.438rem;height:19.625rem}@media (max-width: 767px){.video-frame{width:25rem}}.achivments{display:flex;justify-content:center}@media (max-width: 767px){.achivments{flex-direction:column}}.achivments__icon{display:flex;flex-direction:column;height:10rem;margin:5rem 4rem 0 4rem;text-align:center}

.offer{width:16rem;height:22rem;display:flex;flex-direction:column;align-items:center;position:relative;margin:2rem 0 2rem 2rem;box-shadow:8px 8px rgba(112,112,112,0.1)}.offer__content{display:flex;flex-direction:column;align-items:center}.offer .type{position:absolute;margin-left:0.5rem;top:6px;left:0;padding:4px;font-style:bold;font-weight:bold;background-color:yellow}.image-offer{width:100%}.offer-content{display:flex;flex-direction:column;align-items:center}p:first-child{margin:1rem 0 1rem 0}.slide-list,ul,li{display:flex;flex-direction:row-reverse;list-style:none}@media (max-width: 767px){.slide-list,ul,li{flex-direction:column}}

.offer-container{display:flex;flex-direction:column;align-items:center}

.container-off{width:100%;height:50vh;background-image:url(/tourist/static/media/bgOff.45982fb2.png);background-size:cover;display:flex;flex-direction:column;justify-content:center;position:relative}.container-off__items{z-index:3;display:flex;flex-direction:column;align-items:center}.container-off__items h3,.container-off p{color:#fff;margin:1rem 0 0 0}.container-off__items h3{font-size:2rem}.container-off__overlay{position:absolute;left:0;top:0;right:0;bottom:0;background-color:var(--gradient-color);background-image:-o-linear-gradient(left, var(--gradient-color) 0%, var(--gradient-color2) 100%);background:linear-gradient(to right, var(--gradient-color) 0%, var(--gradient-color2) 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr='$from', endColorstr='$to',GradientType=1 );z-index:1}.container-subscribe{display:flex;margin:1.5rem 0 0 0}.container-subscribe input[type='email']{width:24rem;height:3.125rem;border:2px solid #fff;border-radius:1.563rem;padding:0.5rem;color:#fff;background-color:var(--gradient-color);background-image:-o-linear-gradient(left, var(--gradient-color) 0%, var(--gradient-color2) 100%);background:linear-gradient(to right, var(--gradient-color) 0%, var(--gradient-color2) 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr='$from', endColorstr='$to',GradientType=1 );margin:0 1.2rem 0 0}@media (max-width: 767px){.container-subscribe input[type='email']{width:16rem}}.container-subscribe input[type='email']::-webkit-input-placeholder{color:#fff}.container-subscribe input[type='email']:-ms-input-placeholder{color:#fff}.container-subscribe input[type='email']::-ms-input-placeholder{color:#fff}.container-subscribe input[type='email']::placeholder{color:#fff}.container-subscribe .btn-subscribe{width:9.688rem;height:3.125rem;border:none;border-radius:1.563rem;text-transform:uppercase;background:#fff;color:rgba(8,174,234,0.9)}

.container-testimonials{margin:4.188rem 0 0 0}.testimonials{display:flex;justify-content:center;margin:5.85rem 0 0 0}@media (max-width: 767px){.testimonials{align-items:center}}.container-testimonial{width:23rem;height:25.563rem;margin:0 0 0 1.875rem;background:#cecdcd;display:flex;flex-direction:column;align-items:center}@media (max-width: 767px){.container-testimonial{margin:0 0 0 0}}

.container-destinations{display:flex;flex-direction:column;align-items:center;justify-content:center}.destinations__item{margin:0 1.75rem 0 1.75rem}

.container-footer{display:flex;justify-content:space-around;background:var(--footer-background);color:var(--font-color2)}@media (max-width: 767px){.container-footer{flex-direction:column;align-items:center}}.container-footer__left{display:flex;margin:2rem 0 0 0}@media (max-width: 767px){.container-footer__left{flex-direction:column}}.container-footer__right{margin:1rem 0 8rem 0}@media (max-width: 767px){.container-footer__right>p{text-align:center}}.container-footer input[type='email']{width:17.813rem;height:2.938rem}.container-footer__btn{height:2.938rem;width:3.641rem;background-color:var(--gradient-color);background-image:-o-linear-gradient(left, var(--gradient-color) 0%, var(--gradient-color2) 100%);background:linear-gradient(to right, var(--gradient-color) 0%, var(--gradient-color2) 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr='$from', endColorstr='$to',GradientType=1 );border:none;display:flex;justify-content:center}.email{display:flex}

